import React from "react";
import './info.css';
import ria from '../img/ria.svg';
import Footer from '../components/footer';
import { Link } from "react-router-dom";
const Info = () => {
  return (
    <div>
        <img src={ria}></img>
        <h1>さまざまな情報</h1>
        <h2>このキャラは何?</h2>
        <p>"あとりあざうるす" というオリジナルキャラクターです。</p>
        <h2>開発者情報</h2>
        <table>
          <thead>
            <tr>
              <th colspan="2">開発者情報</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>なまえ</td>
              <td>Atria(あとりあ)</td>
            </tr>
          </tbody>
          <tbody>
            <tr>
              <td>Twitter</td>
              <td><a href="https://twitter.com/AtriaSoft">AtriaSoft</a></td>
            </tr>
          </tbody>
          <tbody>
            <tr>
              <td>GitHub</td>
              <td><a href="https://github.com/Atria64">Atria64</a></td>
            </tr>
          </tbody>
        </table>
        <h2>新年のメッセージ</h2>
        <p>このプロダクトは12/31に1日でReactを学んで作ってみた作品になります。楽しんでくれていれば嬉しいです。</p>
        <p>2022年も、どうぞあとりあをよろしくお願いいたします。</p>
        <Link to="/" className="end">戻る</Link>
        <Footer/>
    </div>
  )
}
export default Info;