import React from "react";
import ria from '../img/ria.svg';
import Footer from '../components/footer';
import { Link } from "react-router-dom";

const Top = () => {
  return (
    <div>
      <div className="top">
        <img src={ria}></img>
        <h1>りあみくじ</h1>
        <h2>あなたの今年の運勢りあを占います</h2>
        <p><Link to="/nameset">はじめる</Link></p>
        <p className="end"><Link to="/info">いろいろな情報</Link></p>
      </div>
      <Footer/>
    </div>
  )
}
export default Top;